    <template>
    <div class="xixixi">
    	<o v-html="msg"></o>
    		<p style="font-family: '楷体'">大</p>
	    	<p class="runningmoney" style="font-size: 116px;width: 134px;margin: 0 auto;font-family: '楷体';font-weight: bolder;color:#8D5053;border-width:2px;border-style:solid;border-color:#8D5053;border-radius:100px">
	    		钱
	    	</p>
	    	<p style="font-family: '楷体'">端</p>
	    	<o v-html="msg"></o>
    	</div>
    	
    </div>
</template>

<script>
export default {
  data () {
    return {
      msg: '<p>&hArr;&hArr;&nbsp;&nbsp;&hArr;&hArr;&nbsp;&nbsp;&hArr;&hArr;&nbsp;&nbsp;&hArr;&hArr;&nbsp;&nbsp;&hArr;&hArr;&nbsp;&nbsp;&hArr;&hArr;</p>'
    }
  }
}
</script>
<style>
    p:nth-child(2n) {
    	font-size: 120px;
    	width: 120px;
    	margin: 0 auto;
    	font-family: '楷体';
    	font-weight: bolder;
    }
    .runningmoney {
    	animation: runHtml 2.5s infinite linear;
    }
    @-moz-keyframes runHtml {
    	0% { -moz-transform: rotateY(0deg); }
    	100% { -moz-transform: rotateY(360deg); }
    }
    @keyframes runHtml {
    	0% { -moz-transform: rotateY(0deg); }
    	100% { -moz-transform: rotateY(360deg); }
    }
    @-webkit-keyframes runHtml {
    	0% { -moz-transform: rotateY(0deg); }
    	100% { -moz-transform: rotateY(360deg); }
    }
</style>


