<template>
  	<div class="hi">
	    <div class="closeli" @click="untouched" v-show="itTouched">
	    	<img class="rotateimg" src="../../src/assets/indextop.png"/>
	    	<div class="backmake">Back&nbsp</div>
	    </div>
	    <el-input @focus="touched" placeholder="请输入歌名或专辑、歌手" icon="search" :on-icon-click="handleIconClick" v-model="keymsg">
	    </el-input>
	    <div class="jiben" v-show="itTouched">
			<div class="vforitem">
				<div v-for="item in newsearchhistory" class="itemspan">{{item}}</div>
			</div>
			<ul>
				<li v-for="(item,index) in hotkey"><span>{{index+1}}&nbsp&nbsp{{item.recomendname}}</span><div class="songnum">热度：{{item.recomendrate}} 万</div></li>
				<!-- <li><span>双世宠妃</span><div class="songnum">8.5</div></li>
				<li><span>从前慢</span><div class="songnum">8.5</div></li>
				<li><span>王俊凯</span><div class="songnum">8.5</div></li>
				<li><span>李宇春</span><div class="songnum">8.5</div></li> -->
			</ul>
		</div>
    	<!-- <dianjihou v-show="itTouched"></dianjihou> -->
    </div>
</template>

<script>
import axios from 'axios'
// import store from '@/vuexinit/store'
import dianjihou from '@/components/sousuolan/dianjihou'
export default {
  components: {
    dianjihou
  },
  // store,
  methods: {
    touched () {
      this.itTouched = true
      this.$emit('whatthefuck')
    },
    untouched () {
      this.keymsg = ''
      this.itTouched = false
      this.$emit('howtofuck')
    },
    handleIconClick () {
      console.log(111)
      this.searchhistory.unshift(this.keymsg)
      this.newsearchhistory = []
      for (let i = 0; i < this.searchhistory.length; i++) {
        if (this.newsearchhistory.indexOf(this.searchhistory[i]) < 0) {
          this.newsearchhistory.push(this.searchhistory[i])
        }
      }
      localStorage.searchhistory = JSON.stringify(this.newsearchhistory)
      // console.log(localStorage)
    }
  },
  created: function () {
    if (localStorage.searchhistory) {
      this.searchhistory = JSON.parse(localStorage.searchhistory)
      this.newsearchhistory = JSON.parse(localStorage.searchhistory)
      console.log(this.searchhistory)
      axios.get('https://www.easy-mock.com/mock/599c9e43059b9c566dcbde29/example/miaoshuyixia')
      .then(response => {
        console.log(response)
        this.hotkey = response.data.songrecomend
      })
    }
  },
  data () {
    return {
      itTouched: false,
      keymsg: '',
      searchhistory: [],
      newsearchhistory: [],
      hotkey: null
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
	.hi {
		margin-top: 10px;
	}
	.el-input {
		/*position: absolute;*/
		width: 68vh!important;
		margin-bottom: 10px!important;
	}
	.el-input__inner {
		background-color: #EEE!important;
		border:none!important;
		height:40px!important;
		margin: 0 10px!important;
		width: calc(100% - 20px)!important;
		font-size: 16px!important;
	}
	.el-input__inner::placeholder {
		color: #6F6F6F!important;
	}
	.el-input__inner::-moz-placeholder {
		color: #6f6f6f!important;
	}
	[class*=" el-icon-"] {
		font-size: 20px!important;
		right: 10px!important;
	}
	.jiben {
		height: 200px;
		background-color: #fff;
		/*position: absolute;*/
		width: 68vh;
	}
	.jiben ul {
		text-align: left;
		list-style: none;
		color: #8391A5;
		font-size: 16px;
		margin-left: 10px;
		/*background: #fff;*/
	}
	.jiben ul li {
		text-align: left;
		margin-bottom: 14px;
		cursor: pointer;
		/*background-color: #fffffe;*/
		/*box-shadow:  0 2px 16px #ddd;*/
	}
	li span {
		width: 50%;
	}
	.songnum {
		float: right;
		text-align: right;
		width: 50%;
		margin-right: 10px;
	}
	.closeli {
		border-radius: 5px 0 0 5px;
		background-color: #bbb;
		color: #000;
		height: 40px;
		margin-left: 10px;
		position: absolute;
		line-height: 40px;
		cursor: pointer;
		z-index: 1;
		/*border: 1px solid #99A9BF;*/
	}
	.itemspan {
		border: 1px solid #99A9BF;
		border-radius: 12px;
		margin-left: 12px;
		margin-bottom: 10px;
		padding-left: 1px;
		padding-right: 1px;
		/*float: left;*/
		color: #99A9BF;
		cursor: pointer;
	}
	.rotateimg {
		transform:rotate(90deg);
		width: 20px;
		margin-top: 10px;
		/*border: 1px solid #99A9BF;*/
	}
	.backmake {
		border-right: 1px dashed #bbb;
		float: right;
	}
	.vforitem {
		/*border: 1px solid #000;*/
		display: flex;
		max-height: 66px;
		flex-wrap: wrap;
		overflow-y: hidden;
	}
</style>


