<template>
  <div id="fpp">
  <!-- <img src="./assets/20150105(1).jpg"> -->
      <sousuolan v-show="allshow" @whatthefuck="middlemenu=false" @howtofuck="middlemenu=true"></sousuolan>
      <biaoqianye v-show="middlemenu&&allshow"></biaoqianye>
      <router-view></router-view>

      
      <transition name="play-slide" @after-enter="某方法" @before-leave="反方法">
        <play v-show="playvue" @howtofuckagain="unshowplayvue"></play>
      </transition>

      <div id="playtouch" v-show="allshow">
        <div class="bottomimgleft bottomimg" @click="showplayvue">
          <img class="imgwhat" src="../src/assets/Vue_Music_Blur.png">
        </div>
        <div class="bottommiddle" @click="showplayvue">VUE MUSIC</div>
        <div class="bottomimgright bottomimg">
          <img class="imghow" src="../src/assets/index.png">
        </div>
      </div>
  </div>
</template>

<script>
import sousuolan from '@/components/sousuolan'
import biaoqianye from '@/components/biaoqianye'
import play from '@/components/play'
export default {
  components: {
    sousuolan,
    biaoqianye,
    play
  },
  data () {
    return {
      middlemenu: true,
      playvue: false,
      allshow: true
    }
  },
  methods: {
    showplayvue () {
      this.playvue = true
      this.allshow = false
    },
    unshowplayvue () {
      this.playvue = false
      this.allshow = true
    }
  }
}
</script>

<style>
  * {
      margin: 0;
      padding: 0;
      text-align: center;
  }
  html {
      /*overflow-x: hidden;*/
      /*background: #fff;*/
      height: 100%;
  }
  body {
      /*导致搜索框变小*/
      display: flex;
      /*overflow-x: hidden;*/
      width: 68vh;
      margin: 0 auto;
      /*background: #fff;*/
      height: 100%;
  }
  #fpp {
    font-family: -apple-system, BlinkMacSystemFont, "PingFang SC", "Helvetica Neue", STHeiti, "Microsoft Yahei", Tahoma, Simsun, sans-serif;
      width: 100%;
      height: 100%;
      color: #42b9c5;
      text-decoration: none;
    }
  .el-tabs__nav {
    width: 100%!important;
  }
  .el-tabs__item {
    width: 50%!important;
  }
  .el-tabs__header {
    margin: 0!important;
  }
  #playtouch {
    background-color: #fff;
    height: 50px;
    width: 68vh;
    position: fixed;
    bottom: 0;
    box-shadow: -0px 1px 50px 0 #ccc;
    /*display: flex*/
  }
  .bottomimgleft {
    /*padding-left: 15px;*/
    float: left;
    margin: 6px 10px 6px 15px;
    /*padding-right: 10px;*/
    width: 38px;
    height: 38px;
    cursor: pointer;
    /*vertical-align: middle;*/
    /*border: 1px solid #000;*/
    /*border-radius: 3px;*/
  }
  .bottomimgright {
    padding-right: 15px;
    float: right;
    width: 38px;
    height: 46px;
/*    padding-top: 11px;
    padding-bottom: 11px;*/
    /*vertical-align: middle;*/
    /*border: 1px solid #000;*/
  }
  .bottommiddle {
    float: left;
    line-height: 20px;
    color: #000;
    height: 20px;
    margin: 15px auto;
    cursor: pointer;
    /*border: 1px solid #000;*/
  }
  .imgwhat {
    height: 38px;
    border-radius: 4px;
  }
  .imghow {
    height: 24px;
    margin-top: 14px;
    cursor: pointer;
  }
</style>



