<template>
	<div class="jiben" v-show="itTouched">
		<ul>
			<li><div class="closeli" @click="untouched">close</div></span></li>
			<li><span>无条件</span><div class="songnum">8.5</div></li>
			<li><span>双世宠妃</span><div class="songnum">8.5</div></li>
			<li><span>从前慢</span><div class="songnum">8.5</div></li>
			<li><span>王俊凯</span><div class="songnum">8.5</div></li>
			<li><span>李宇春</span><div class="songnum">8.5</div></li>
		</ul>
	</div>
</template>
<script type="text/javascript">
  export default {
    methods: {
      untouched () {
        this.itTouched = false
      }
    },
    data () {
      return {
        itTouched: true
      }
    }
  }
</script>
<style type="text/css">
	.jiben {
		height: 160px;
		background-color: #fff;
		/*position: absolute;*/
		width: 68vh;
	}
	ul {
		text-align: left;
		list-style: none;
		color: #8391A5;
		font-size: 16px;
		margin-left: 10px;
	}
	.jiben ul li {
		text-align: left;
		margin: 12px auto;
	}
	li span {
		width: 50%;
	}
	.songnum {
		float: right;
		text-align: right;
		width: 50%;
		margin-right: 10px;
	}
/*	.closeli {
		border-radius: 5px;
		background-color: #ddd;
		width: 46px;
		margin: 0 auto;
	}*/
</style>


