<template>
	<div class="play">
		<div class="toptop">
			<div class="playbigimgdiv">
				<div class='fuckfuckfuck'>
					<img class="playbigimg" src="../../src/assets/Vue_Music_Blur.png">
				</div>
			</div>
			<div class="playsmallimgdiv" @click="smallclick">
				<img class="playsmallimg" src="../../src/assets/indextop.png">
			</div>
		</div>
		<div class="middlemiddle">
			<div class="middleone"></div>
			<div class="middletwo">
				<span class="spanone">0:00</span>
				<span class="spantwo">0:00</span>
			</div>
			<div class="middlethr">VUE MUSIC</div>
		</div>
		<div class="bottombottom">
			<img class="bottomone bottomtwo" src="../../src/assets/icon-like.png">
			<img class="bottomone" src="../../src/assets/indexleft.png">
			<img class="bottomone" src="../../src/assets/index.png">
			<img class="bottomone" src="../../src/assets/indexright.png">
			<img class="bottomone bottomthr" src="../../src/assets/icon-list.png">
		</div>
	</div>
</template>
<script>
  export default {
    data () {
      return {
        musicplay: true
      }
    },
    methods: {
      smallclick () {
        // this.musicplay = false
        this.$emit('howtofuckagain')
        // this.$parent.allshow = true
      }
    }
  }
</script>
<style>
	.play {
		height: 100%;
		width: 68vh;
		/*border: 1px solid #000;*/
		background-color: #fff;
		/*z-index: 3;*/
	}
	.toptop {
		position: absolute;
	}
	.playbigimgdiv {
		position: absolute;
	}
	.playbigimg {
		width: 68vh;
	}
	.playsmallimgdiv {
		margin-top: 28px;
		/*margin-left: 8px;*/
		position: relative;
		/*z-index: 5;*/
		/*padding-top: 26px;*/
		/*padding-left: 6px;*/
		margin-left: 10px;
		/*text-align: left;*/
		/*border: 1px solid #000;*/
		width: 24px;
		height: 24px;
		border-radius: 13px;
		background-color: #fff;
	}
	.playsmallimg {
		width: 13px;
		height: 13px;
		vertical-align: middle;
		/*border: 1px solid #000;*/
	}
	.middlemiddle {
		height: 60px;
		/*border: 1px solid #000;*/
		position: relative;
		top: 68vh;
		color: #000;
	}
	.middleone {
		height: 4px;
		background-color: #7F7F7F;
	}
	.middletwo {
		margin: 7px auto;
		font-size: 12px;
		/*border: 1px solid #000;*/
	}
	.spanone {
		float: left;
		margin-left: 8px;
	}
	.spantwo {
		float: right;
		margin-right: 8px;
	}
	.middlethr {
		font-size: 18px;
		font-weight: bold;
		/*border: 1px solid #000;*/
		margin-top: 30px;
	}
	.bottombottom {
		/*border: 1px solid #000;*/
		width: 68vh;
		height: 50px;
		bottom: 0;
		position: fixed;
	}
	.bottomone {
		height: 35px;
		margin: 0 22px; 
	}
	.bottomtwo {
		float: left; 
		margin-left: 10px!important;
	}
	.bottomthr {
		float: right; 
		margin-right: 10px!important;
	}
</style>


