<template>
  <div class="block">
    <el-carousel trigger="click" height="172px">
      <el-carousel-item  v-for="banderitems in bandermessages" :key="banderitems">
        <!-- <h3>{{ item }}</h3> -->
        <img style="height:172px" :src="banderitems.pic">
      </el-carousel-item>
    </el-carousel>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  data () {
    return {
      activeName: 'first',
      bandermessages: []
    }
  },
  created: function () {
    axios.get('https://www.easy-mock.com/mock/599c9e43059b9c566dcbde29/example/bander')
    .then(response => {
      this.bandermessages = response.data.focus
      console.log(this.bandermessages)
    })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  .el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 150px;
    margin: 0;
  }

  .el-carousel__item:nth-child(2n) {
     background-color: #99a9bf;
  }
  
  .el-carousel__item:nth-child(2n+1) {
     background-color: #d3dce6;
  }
  .el-carousel__button {
    width: 10px!important;
    height: 4px!important;
    transition: 1.2s!important;
    border-radius: 2px!important;
  }
</style>


