<template>
  <div class="hi">
  	<el-tabs v-model="activeName" @tab-click="handleClick">
    <el-tab-pane label="推荐" name="first">
    	<div>
    		<bander></bander>
    	</div>
    	<div>
    		<songlist></songlist>
    	</div>
    </el-tab-pane>
    <el-tab-pane label="排行榜" name="second">
      <div>
        <rankshow></rankshow>
      </div>
    </el-tab-pane>
  </el-tabs>
  </div>
</template>

<script>
import bander from '@/components/ofbiaoqian/bander'
import songlist from '@/components/ofbiaoqian/songlist'
import rankshow from '@/components/ofbiaoqian/rankshow'
export default {
  components: {
    bander,
    songlist,
    rankshow
  },
  data () {
    return {
      activeName: 'first'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  .el-tabs__item.is-active {
    color: #000!important;
  }
  .el-tabs__item {
    color: #999999!important;
    font-size: 16px!important;
  }
  .el-tabs__header {
    border-bottom: none!important;
  }
  .el-tabs__active-bar {
    background-color: #fff!important;
  }
</style>


