<template>
  <div class="ulback">
    <p vertical-align="middle" style = "margin: 13px 0; color: #000">本周热门</p>
    <div class="ulback">
      <ul>
        <li :class="indexnum + 'onEnd' + songs.index" v-for="(songs,key) in songmessages">
          <img :src="songs.imgurl" width="100%"><br>
          <span style="color:#001">{{songs.author}}</span>
          <br>
          <small><span style="color:#666666">{{songs.dissname}}</span></small>
        </li>
      </ul>
      <div style = "width: 100%; margin-top: 55px; float: left" name="This's an intresting bug of CSS wrote by me."></div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
// Vue.use(VueJsonp)
export default {
  data () {
    return {
      indexnum: 'what',
      songmessages: [
        {songname: '第一首歌',songman: '一一',songhref: '../../../src/assets/20150105(1).jpg'},
        {songname: '第二首歌',songman: '二二',songhref: '../../src/static/img/20150105(1).jpg'},
        {songname: '第三首歌',songman: '三三',songhref: '.../../src/static/img/20150105(1).c1092dd.jpg'}
      ]
    }
  },
  created: function () {
    axios.get('https://www.easy-mock.com/mock/599c9e43059b9c566dcbde29/example/mainlist')
    .then(response => {
      // console.log(1,response)
      this.songmessages = response.data.hotdiss.list
      for (let i = 0; i < this.songmessages.length; i++) {
        this.songmessages[i].index = 'index' + i % 2
        // console.log(this.songmessages[1])
      }
    })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .ulback {
    /*background-color: black;*/
    width: 100%;
  }
  ul {
    list-style: none;
    /*background-color: #fff;*/
    border-radius: 1%;
    /*height: 100%;*/
  }
  li {
    /*float: left;*/  
    text-align: left;
    width: 49%;
    margin-top: 1vh;
    margin-left: 0;
    /*background-color: #fff;*/
    box-shadow:1px 1px 16px 0 #ccc;
    border-radius: 2%;
    min-height: 280px;
  }
  img {
    border-radius: 2%;
    /*border: px ridge #000;*/
    box-shadow:50px 50px 50px 50px #999 inset;
  }
/*  li[class$="0"] {
    float: left;
  }
  li[class$="1"] {
    float: right;
  }*/
  li:nth-child(2n-1) {
    float: left;
  }
  li:nth-child(2n) {
    float: right;
  }
/*  li[class~="type"] {
    float: right;
  }*/
  /*p {
    margin:30px 0;
  }*/
</style>


